export default {
  layout: "admin",
  data() {
    return {
      loading: false,
      activeName: "register",
      isAgree: false,
      registerForm: {
        company_name: "",
        taxe_id: "",
        phone: "",
        password: "",
      },
      registerRules: {
        company_name: [
          { required: true, message: "请输入公司名称", trigger: "blur" },
          {
            min: 3,
            max: 20,
            message: "长度在 3 到 5 个字符",
            trigger: "blur",
          },
        ],
        taxe_id: [
          { required: true, message: "请输入纳税识别号", trigger: "blur" },
        ],
        phone: [
          { required: true, message: "请输入联系方式", trigger: "blur" },
          {
            pattern: /^\d{8,11}$/,
            message: "联系方式为8位数座机号或者11位手机号",
            trigger: "blur",
          },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
      loginForm: {
        company_name: "",
        password: "",
      },
      loginRules: {
        company_name: [
          { required: true, message: "请输入公司名称", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    toggleAgree() {
      this.isAgree = !this.isAgree;
    },
    submitRegister() {
      if (!this.isAgree) {
        this.msg && this.msg.close();
        this.msg = this.$message({
          message: "请同意《注册声明》",
          type: "warning",
        });
        return;
      }
      this.$refs.register_form.validate((valid) => {
        if (valid) {
          console.log("ok");
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            const msgInstance = this.$message({
              message: "注册成功",
              type: "success",
              duration: 1000,
              onClose: () => {
                this.activeName = "login";
              },
            });
          }, 3000);
        } else {
          console.log(valid);
        }
      });
    },
    submitLogin() {
      this.$refs.logn_form.validate((valid) => {
        if (valid) {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.$message({
              message: "登录成功",
              duration: 1000,
              type: "success",
              onClose: () => {
                this.$router.push({
                  path: "/user",
                });
              },
            });
          }, 3000);
        }
      });
    },
  },
};
